import { useState, useEffect } from 'react';
import { getPlayerStats } from '../utils/api';
import styles from '../stylesheets/TopPerformers.module.css'; // Import the CSS module

const TopPerformers = () => {
    const [playerStats, setPlayerStats] = useState([]);
    const [selectedPlayer, setSelectedPlayer] = useState(null);

    useEffect(() => {
        const fetchPlayerStats = async () => {
            const stats = await getPlayerStats();
            setPlayerStats(stats);
        };
        fetchPlayerStats();
    }, []);

    const totalGoals = (player) => {
        return player.performance.reduce((sum, perf) => sum + perf.goalsScored, 0);
    };

    const totalAssists = (player) => {
        return player.performance.reduce((sum, perf) => sum + perf.assists, 0);
    };

    const totalMinutes = (player) => {
        return player.performance.reduce((sum, perf) => sum + perf.minutesPlayed, 0);
    };

    const totalMatches = (player) => {
        return player.performance.length;
    };

    const theMatches = (player) => {
        const matchIds = player.performance.map((item) => item.matchId);
        return matchIds.join(', ');
    };

    const topActualPerformers = playerStats
        .sort((a, b) => totalGoals(b) - totalGoals(a))
        .slice(0, 3);
    const topPerformers = topActualPerformers;

    return (
        <div className={styles.container}>
            <h1 className={styles.styleForh1}>Top Performers</h1>
            {/*<h4 className={styles.topPerformersHead}>Top Performers</h4>*/}
            <div className={styles.topPerformers}>
                {topPerformers.map((player, index) => (
                    <div key={index} className={styles.playerCard}>
                        <h2>{player.name}</h2>
                        <div style={{display: 'flex', gap: '10px'}}>
                            <p className={styles.statsHead}>Stats:</p>
                            <p>{totalGoals(player)} Goals </p>
                            <p>{totalAssists(player)} Assists </p>
                            <p>{totalMinutes(player)} Minutes Played </p>
                        </div>

                    </div>
                ))}
            </div>
        </div>
    )
}

export default TopPerformers