import React, { useState, useEffect } from 'react';
import styles from '../../stylesheets/PlayersForMatch.module.css';
import { getPlayersForMatch } from '../../utils/api';
import UnderProgress from "../UnderProgress";

const PlayersForMatch = ({ matchesParam, players }) => {
    const [selectedMatch, setSelectedMatch] = useState(null);
    const [playersOfMatch, setPlayersOfMatch] = useState([]);
    //console.log("these are matches",matches)


    useEffect(() => {
        if (matchesParam && matchesParam.length > 0) {
            setSelectedMatch(matchesParam[0]);
        }
    }, [matchesParam]);
    useEffect(() => {
        if (selectedMatch) {
            const fetchPlayers = async () => {
                try {
                    const playerDetails = await getPlayersForMatch(selectedMatch.matchId);
                    setPlayersOfMatch(playerDetails);
                    //console.log(playersOfMatch)
                } catch (error) {
                    console.error('Error fetching players:', error);
                }
            };
            fetchPlayers();
        }
    }, [selectedMatch]);


    if (!matchesParam || matchesParam.length === 0) {
        return <p>No matches available.</p>;
    }
    return (
        selectedMatch &&
        (
        <div className={styles.matchAndPlayerListContainer}>
            <h2 className={styles.title}>Players Involved in Given Match</h2>
            <select value={selectedMatch?.matchId} onChange={(e) => setSelectedMatch(matchesParam.find(match => match.matchId === e.target.value))}>
                {matchesParam?.map(match => (
                    <option key={match.matchId} value={match.matchId}>
                        {match.matchId} - {match.teamA} vs {match.teamB} - {match.date.split('T')[0]}
                    </option>
                ))}
            </select>
            {/*<UnderProgress />*/}
            

            <ul className={styles.playerList}>


                {playersOfMatch.length > 0 ? (
                    playersOfMatch?.map(player => (
                        <li key={player.name} className={styles.playerItem}>
                            <h3>{player.name}</h3>
                            <p>Position: {player.position}</p>
                            <p>Current Team: {player.currentTeam}</p>
                            <p>Nationality: {player.nationality}</p>
                        </li>
                    ))
                ) : (
                    <p>No players found for the selected match.</p>
                )}





            </ul>
        </div>)

    );
};

export default PlayersForMatch;
