import {Link} from 'react-router-dom';
import {logout} from '../utils/auth'
import {useNavigate} from "react-router-dom";
import styles from '../stylesheets/Dashboard.module.css'



import { useState, useEffect } from 'react';
import { getMatchDetails, getPlayerStats } from '../utils/api';
import MatchList from '../components/componentsForDashboard/MatchList';
import PlayerList from '../components/componentsForDashboard/PlayerList';
import MatchPlayerDetails from '../components/componentsForDashboard/MatchPlayerDetails';
import styles2 from '../stylesheets/MainPageElementsContainer.module.css';
//import PlayersForMatchVerticalScroll from "../components/componentsForDashboard/PlayersForMatchVerticalScroll";
import PlayersForMatch from "../components/componentsForDashboard/PlayersForMatch";
import UnderProgress from "../components/UnderProgress";

const Dashboard = () => {

    //added
    const [matches, setMatches] = useState([]);
    const [players, setPlayers] = useState([]);
    const [selectedMatch, setSelectedMatch] = useState(null);
    const [selectedPlayer, setSelectedPlayer] = useState(null);





    const navigate = useNavigate()
    const handleLogout = async () => {
        await logout();
        navigate('/');
        navigate(0)

    }



    //added

    useEffect(() => {
        const fetchData = async () => {
            try {
                const matchDetails = await getMatchDetails();
                const playerDetails = await getPlayerStats();
                setMatches(matchDetails);
                setPlayers(playerDetails);
                if (matchDetails.length > 0) {
                    setSelectedMatch(matchDetails[0]); // Set default selected match
                }
                if (playerDetails.length > 0) {
                    setSelectedPlayer(playerDetails[0]); // Set default selected player
                }
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };
        fetchData();
    }, []);

    return(
        <div className={styles.dashboardContainer}>
            <h1>Dashboard</h1>
            <nav>
                <div>
                    <h2 className={styles.brandHeading}>GameDay Analytics</h2>
                </div>
                <ul className={styles.dashboardNavUl}>
                    <li><Link to="/player-stats">Player Stats</Link></li>
                    <li><Link to="/match-details">Match Details</Link></li>
                    <li><Link to="/analysis">Analysis</Link></li>

                </ul>
                <div className={styles.rightLink}>
                    <button onClick={handleLogout}>Logout</button>
                </div>

            </nav>

            <div className={styles2.mainPage}>

                <MatchList matches={matches} onSelectMatch={setSelectedMatch} />
                {/*<UnderProgress /> */}
                
                <PlayerList players={players} onSelectPlayer={setSelectedPlayer} />
                <PlayersForMatch matchesParam={matches}  players={players} />
            </div>
        </div>
    )
}

export default Dashboard
