import React, { useState, useEffect } from 'react';
import { getMatchDetails } from '../utils/api';
import { Bar } from 'react-chartjs-2';
import Chart from 'chart.js/auto'; // Import Chart.js components
import styles from '../stylesheets/GroupedBarGraph.module.css'

const GroupedBarChart = () => {
    const [allMatches, setAllMatches] = useState([]);
    const [selectedTeams, setSelectedTeams] = useState('Liverpool,Manchester City');
    const [chartData, setChartData] = useState(null);

    const handleMatchSelect = (event) => {
        const teams = event.target.value;
        setSelectedTeams(teams);
    };

    useEffect(() => {
        const fetchMatchDetails = async () => {
            try {
                const matches = await getMatchDetails();
                setAllMatches(matches);
            } catch (error) {
                console.error('Error fetching match details:', error);
            }
        };
        fetchMatchDetails();
    }, []);

    useEffect(() => {
        if (selectedTeams) {
            const [teamA, teamB] = selectedTeams.split(',');

            const filteredMatches = allMatches.filter(match =>
                (match.teamA === teamA && match.teamB === teamB) ||
                (match.teamA === teamB && match.teamB === teamA)
            );

            // Process data for Chart.js
            const monthlyData = filteredMatches.reduce((acc, match) => {
                const matchDate = new Date(match.date);
                const monthYear = `${matchDate.getFullYear()}-${matchDate.getMonth() + 1}`;
                const [scoreA, scoreB] = match.score.split('-').map(Number);

                if (!acc[monthYear]) {
                    acc[monthYear] = { teamA: 0, teamB: 0, labels: [] };
                }

                acc[monthYear].teamA += (match.teamA === teamA ? scoreA : scoreB);
                acc[monthYear].teamB += (match.teamA === teamB ? scoreA : scoreB);
                if (!acc[monthYear].labels.includes(match.matchId)) {
                    acc[monthYear].labels.push(match.matchId);
                }

                return acc;
            }, {});

            const months = Object.keys(monthlyData);
            const teamAData = months.map(month => monthlyData[month].teamA);
            const teamBData = months.map(month => monthlyData[month].teamB);
            const labels = months.map(month => monthlyData[month].labels.join(', '));

            setChartData({
                labels: labels,
                datasets: [
                    {
                        label: 'Team A',
                        data: teamAData,
                        backgroundColor: 'rgba(75, 192, 192, 0.2)',
                        borderColor: 'rgba(75, 192, 192, 1)',
                        borderWidth: 1
                    },
                    {
                        label: 'Team B',
                        data: teamBData,
                        backgroundColor: 'rgba(153, 102, 255, 0.2)',
                        borderColor: 'rgba(153, 102, 255, 1)',
                        borderWidth: 1
                    }
                ]
            });
        }
    }, [selectedTeams, allMatches]);

    return (
        <div className={styles.graphContainer}>
            <div className={styles.teamSelection}>
                <p style={{fontWeight: 'bold'}}>Select teams for graphical analysis over different matches</p>
                <select id="matchSelect" onChange={handleMatchSelect} value={selectedTeams}>
                    <option value="">--Select Match--</option>
                    {allMatches
                        .map(match => [match.teamA, match.teamB])
                        .reduce((acc, pair) => {
                            const key = `${pair[0]}-${pair[1]}`;
                            if (!acc.includes(key)) {
                                acc.push(key);
                            }
                            return acc;
                        }, [])
                        .map(pair => {
                            const [teamA, teamB] = pair.split('-');
                            return (
                                <option key={`${teamA}-${teamB}`} value={`${teamA},${teamB}`}>
                                    {teamA} vs {teamB}
                                </option>
                            );
                        })}
                </select>
            </div>

            {chartData && (
                <div className={styles.actBarContainer}>
                    <Bar
                        data={chartData}
                        options={{
                            responsive: true,
                            plugins: {
                                legend: {
                                    position: 'top',
                                },
                                tooltip: {
                                    callbacks: {
                                        label: function(context) {
                                            return `${context.dataset.label}: ${context.raw}`;
                                        }
                                    }
                                }
                            }
                        }}
                    />
                </div>
            )}
        </div>
    );
};

export default GroupedBarChart;
