import axios from 'axios';
const API_URL = 'http://localhost:5000/api';
const {getToken} = require('./auth')

export const login = async (email, password) => {

    try{
        const response = await axios.post(`${API_URL}/auth/login`, {email, password});
        //console.log(response.data)
        return response.data
    }catch (error) {
        return error
    }

};


export const register = async (name, email, password) => {
    const response = await axios.post(`${API_URL}/auth/register`,{name, email, password});
    return response.data;
}

export const getPlayerStats = async () => {
    //console.log(getToken())
    const response = await axios.get(`${API_URL}/players/stats`,{
        headers: {
            'Authorization': `Bearer ${getToken()}`
        }
    });
    return response.data;
};
export const getMatchDetails = async () => {
    //console.log('this api was hit:::' ,`${API_URL}/matches/details` )
    const response = await axios.get(`${API_URL}/matches/details`,{
        headers:{
            'Authorization': `Bearer ${getToken()}`
        }
    });
    //console.log(response.data)
    return response.data;
}
export const getPlayerPerformance = async (playerId) => {
    const response = await axios.get(`${API_URL}/analysis/player/${playerId}`);
    return response.data;
}

export const getMatchPrediction = async (teamA, teamB) => {
    const response = await axios.post(`${API_URL}/analysis/match-prediction`,

    {teamA, teamB},
        {headers:{
                'Authorization': `Bearer ${getToken()}`
            }});
    return response.data;
}

export const getPlayersForMatch= async (matchId) => {


    try{
        const response = await axios.get(`${API_URL}/matches/players-for-match/${matchId}`,

            {headers:{
                    'Authorization': `Bearer ${getToken()}`
                }});
        return response.data;
    }catch (error) {
        console.error('Error fetching players for match:', error);
        throw error;
    }



}

