import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, LineElement, PointElement, Title, Tooltip, Legend } from 'chart.js';
import { getPlayerStats} from "../utils/api";
import styles from "../stylesheets/GroupedBarGraph.module.css";

ChartJS.register(
    CategoryScale,
    LinearScale,
    LineElement,
    PointElement,
    Title,
    Tooltip,
    Legend
);

const LineChart = () => {
    const [playersArray, setPlayersArray] = useState([]);
    const [selectedPlayer, setSelectedPlayer] = useState(null);

    useEffect(() => {
        const fetchPlayerStats = async () => {
            const stats = await getPlayerStats();
            setPlayersArray(stats);
            if (stats.length > 0) setSelectedPlayer(stats[0]._id); // Default to the first player
        };
        fetchPlayerStats();
    }, []);

    const handlePlayerChange = (event) => {
        setSelectedPlayer(event.target.value);
    };

    const getPlayerData = () => {
        if (!selectedPlayer) return { matches: [], goals: [], scores: [] };

        const player = playersArray.find(player => player._id === selectedPlayer);
        if (!player) return { matches: [], goals: [], scores: [] };

        const matches = player.performance.map(p => p.matchId);
        const goals = player.performance.map(p => p.goalsScored);
        const scores = player.performance.map(p => p.goalsScored + p.assists); // Total performance score

        return { matches, goals, scores };
    };

    const { matches, goals, scores } = getPlayerData();

    const data = {
        labels: matches,
        datasets: [
            {
                label: 'Goals Scored',
                data: goals,
                borderColor: 'rgba(75, 192, 192, 1)',
                backgroundColor: 'rgba(75, 192, 192, 0.2)',
                fill: true,
            },
            {
                label: 'Total Performance Score',
                data: scores,
                borderColor: 'rgba(255, 99, 132, 1)',
                backgroundColor: 'rgba(255, 99, 132, 0.2)',
                fill: true,
            }
        ]
    };

    return (
        <div className={styles.graphContainer}>
            <div className={styles.teamSelection}>
                <p style={{fontWeight: 'bold'}}>Select player for graphical analysis over different matches</p>
            <select onChange={handlePlayerChange} value={selectedPlayer}>
                {playersArray.map(player => (
                    <option key={player._id} value={player._id}>
                        {player.name}
                    </option>
                ))}
            </select>
            </div>
            <div className={styles.actBarContainer}>
                <Line data={data} options={{ responsive: true }} />
            </div>
        </div>
    );
};

export default LineChart;
