import React, { useState, useEffect } from 'react';
import { getMatchDetails, getMatchPrediction } from '../utils/api';
import styles from '../stylesheets/MatchPrediction.module.css';

const MatchPrediction = () => {
    const [matchPrediction, setMatchPrediction] = useState(null);
    const [selectedTeams, setSelectedTeams] = useState('Liverpool,Manchester City');
    const [allMatches, setAllMatches] = useState([]);

    const handleMatchSelect = (event) => {
        const teams = event.target.value;
        //console.log(teams);
        setSelectedTeams(teams);
    };

    useEffect(() => {
        const fetchPrediction = async () => {
            if (selectedTeams) {
                const [teamA, teamB] = selectedTeams.split(',');
                const prediction = await getMatchPrediction(teamA, teamB);
                setMatchPrediction(prediction);
                //console.log(prediction);
            }
        };

        fetchPrediction();

    }, [selectedTeams]);

    useEffect(() => {
        const fetchMatchDetails = async () => {
            try {
                const details = await getMatchDetails();
                //console.log(details);
                setAllMatches(details);


            } catch (error) {
                console.error('Error fetching match details:', error);
            }
        };
        fetchMatchDetails();
    }, []);

    //if (!matchPrediction) return null;


    const uniqueMatches = new Set();
    const distinctMatches = allMatches.filter(match => {
        // Normalize team order to create a unique key
        const teams = [match.teamA, match.teamB].sort();
        const matchKey = `${teams[0]}-${teams[1]}`;
        if (!uniqueMatches.has(matchKey)) {
            uniqueMatches.add(matchKey);
            return true;
        }
        return false;
    });

    return (
        <div className={styles.container}>
            <h2 className={styles.header}>Match Prediction</h2>

            <div className={styles.teamSelection}>
            <p>Select teams</p>
            <select id="matchSelect" onChange={handleMatchSelect} value={selectedTeams}>
                <option value="">--Select Teams--</option>
                {distinctMatches.map((match) => (
                    <option key={match.matchId} value={`${match.teamA},${match.teamB}`}>
                        {match.teamA} vs {match.teamB}
                    </option>
                ))}
            </select>
            </div>

            {matchPrediction && (
                <div>

                    <p><span className={styles.selectedTeam}>Team A:</span> {selectedTeams.split(',')[0]}</p>
                    <p><span className={styles.selectedTeam}>Team B:</span> {selectedTeams.split(',')[1]}</p>

                    <div className={styles.stats}>
                        <div className={styles.stat}>
                            <p className={styles.statTitle}>Wins for Team A</p>
                            <p>{matchPrediction.winsA}</p>
                        </div>
                        <div className={styles.stat}>
                            <p className={styles.statTitle}>Wins for Team B</p>
                            <p>{matchPrediction.winsB}</p>
                        </div>
                        <div className={styles.stat}>
                            <p className={styles.statTitle}>Draws</p>
                            <p>{matchPrediction.draws}</p>
                        </div>
                        <div className={styles.stat}>
                            <p className={styles.statTitle}>Total Matches</p>
                            <p>{matchPrediction.totalMatches}</p>
                        </div>
                    </div>
                </div>

            )}
        </div>
    );
};

export default MatchPrediction;
