import React from 'react';
import styles from '../../stylesheets/PlayerList.module.css';
import UnderProgress from "../UnderProgress"; // Import the CSS module

const PlayerList = ({ players, onSelectPlayer }) => {
    return (
        <div className={styles.playerListContainer}>
            <h2 className={styles.title}>Player Details</h2>
            {/*<UnderProgress />*/}
            
            <ul className={styles.playerList}>
                {players?.map(player => (
                    <li key={player.name} className={styles.playerItem} onClick={() => onSelectPlayer(player)}>
                        <h3>{player.name}</h3>
                        <p>Position: {player.position}</p>
                        <p>Current Team: {player.currentTeam}</p>
                        <p>Nationality: {player.nationality}</p>
                        <p>Date of Birth: {new Date(player.dateOfBirth).toLocaleDateString()}</p>
                    </li>
                ))}
            </ul>
            
        </div>
    );
};

export default PlayerList;
