
//import './App.css';
import {BrowserRouter as Router, Routes, Route, Navigate} from 'react-router-dom';
import Login from './pages/Login';
import Register from './pages/Register'
import Dashboard from './pages/Dashboard'
import PlayerStats from './pages/PlayerStats'
import MatchDetails from './pages/MatchDetails'
import Analysis from './pages/Analysis'
import {isAuthenticated} from './utils/auth'

function App() {
  return (
    <Router>
        <Routes>

            <Route path = "/dashboard" element={isAuthenticated() ? <Dashboard /> : <Navigate to='/login'/>} />
            <Route path = "/player-stats" element={isAuthenticated() ? <PlayerStats /> : <Navigate to='/login'/>} />
            <Route path = "/match-details" element={isAuthenticated() ? <MatchDetails /> : <Navigate to='/login'/>} />
            <Route path = "/analysis" element={isAuthenticated() ? <Analysis /> : <Navigate to='/login'/>} />
            <Route path = "/login" element={isAuthenticated() ? <Dashboard /> : <Login />} />
            <Route path = "/register" element={isAuthenticated() ? <Dashboard /> : <Register />} />
            <Route path = "/" element={isAuthenticated() ? <Dashboard /> : <Login /> } />
        </Routes>
    </Router>
  );
}

export default App;
