import React from 'react';
import styles from '../../stylesheets/MatchList.module.css'; // Import the CSS module

const MatchList = ({ matches, onSelectMatch }) => {
    //console.log(matches)
    return (
        <div className={styles.matchListContainer}>
            <h2 className={styles.title}>Match Details</h2>
            <ul className={styles.matchList}>
                {matches?.map(match => (
                    <li key={match.matchId} className={styles.matchItem} onClick={() => onSelectMatch(match)}>
                        <h3>{match.teamA} vs {match.teamB}</h3>
                        <p>Date: {new Date(match.date).toLocaleDateString()}</p>
                        <p>Score: {match.score}</p>
                    </li>
                ))}
            </ul>
        </div>
    );
};

export default MatchList;
