import { useState, useEffect } from 'react';
import { getPlayerStats } from '../utils/api';
import styles from '../stylesheets/PlayerStats.module.css';
import UnderProgress from "../components/UnderProgress";
// Import the CSS module

const PlayerStats = () => {
    const [playerStats, setPlayerStats] = useState([]);
    const [selectedPlayer, setSelectedPlayer] = useState(null);

    useEffect(() => {
        const fetchPlayerStats = async () => {
            const stats = await getPlayerStats();
            setPlayerStats(stats);
            setSelectedPlayer(stats[0])
        };
        fetchPlayerStats();
    }, []);

    const totalGoals = (player) => {
        return player.performance.reduce((sum, perf) => sum + perf.goalsScored, 0);
    };

    const totalAssists = (player) => {
        return player.performance.reduce((sum, perf) => sum + perf.assists, 0);
    };

    const totalMinutes = (player) => {
        return player.performance.reduce((sum, perf) => sum + perf.minutesPlayed, 0);
    };

    const totalMatches = (player) => {
        return player.performance.length;
    };

    const theMatches = (player) => {
        const matchIds = player.performance.map((item) => item.matchId);
        return matchIds.join(', ');
    };

    const topActualPerformers = playerStats
        .sort((a, b) => totalGoals(b) - totalGoals(a))
        .slice(0, 3);
    const topPerformers = topActualPerformers;

    return (
        <div>
            {/*<UnderProgress />*/}
            
            <h2 className={styles.absoluteHeading}>GameDay Analytics</h2>
            <div className={styles.spacingDiv}></div>
        <div className={styles.container}>
            <h1 className={styles.styleForh1}>Player Stats</h1>
            <h4 className={styles.topPerformersHead}>Top Performers</h4>
            <div className={styles.topPerformers}>
                {topPerformers.map((player, index) => (
                    <div key={index} className={styles.playerCard}>
                        <h2>{player.name}</h2>
                        <p className={styles.statsHead}>Stats:</p>
                        <p>{totalGoals(player)} Goals</p>
                        <p>{totalAssists(player)} Assists</p>
                        <p>{totalMinutes(player)} Minutes Played</p>
                    </div>
                ))}
            </div>
            <div className={styles.playerSelector}>
                <label htmlFor="player-select">Select Player:</label>
                <select
                    id="player-select"
                    onChange={(e) => setSelectedPlayer(playerStats.find(player => player.name === e.target.value))}
                    value={selectedPlayer?.name}
                >
                    <option value="">--Select Player--</option>
                    {playerStats.map((player, index) => (
                        <option key={index} value={player.name}>{player.name}</option>
                    ))}
                </select>
            </div>
            {selectedPlayer && (
                <div className={styles.selectedPlayerStatsContainer}>
                    <h2 className={styles.playerNameHead}>{selectedPlayer.name}</h2>
                    <div className={styles.selectedPlayerStats}>
                        <p className={styles.statsHead}>--Stats--</p>
                        <div className={styles.scoreContainer}>

                            <p><strong>{totalGoals(selectedPlayer)}</strong> Goals</p>
                            <p><strong>{totalAssists(selectedPlayer)}</strong> Assists</p>
                            <p><strong>{totalMinutes(selectedPlayer)}</strong> Minutes Played</p>
                        </div>
                        <div className={styles.additionalDetails}>
                            <p><span>Nationality:</span> {selectedPlayer.nationality}</p>
                            <p><span>Born on:</span> {selectedPlayer.dateOfBirth.split('T')[0]}</p>
                            <p><span>Current team:</span> {selectedPlayer.currentTeam}</p>
                        </div>
                        <div className={styles.matchDetails}>
                            <p><span>Matches played:</span> {totalMatches(selectedPlayer)}</p>
                            <p><span>Matches:</span> {theMatches(selectedPlayer)}</p>
                            <p><span>Position:</span> {selectedPlayer.position}</p>
                        </div>
                    </div>
                </div>
            )}
        </div>
        
        </div>
    );
};

export default PlayerStats;
