import { useState, useEffect } from 'react';
import { getMatchDetails } from '../utils/api';
import styles from '../stylesheets/MatchDetails.module.css';
import UnderProgress from "../components/UnderProgress"; // Import the CSS module

const MatchDetails = () => {
    const [matchDetails, setMatchDetails] = useState([]);
    const [selectedMatch, setSelectedMatch] = useState(null);

    useEffect(() => {
        const fetchMatchDetails = async () => {
            try {
                const details = await getMatchDetails();
                console.log(details);
                setMatchDetails(details);
                if (details.length > 0) {
                    setSelectedMatch(details[0]); // Set default selected match
                }
            } catch (error) {
                console.error('Error fetching match details:', error);
            }
        };
        fetchMatchDetails();
    }, []);

    const handleMatchSelect = (event) => {
        const matchId = event.target.value;
        const match = matchDetails.find(m => m.matchId === matchId);
        setSelectedMatch(match);
    };

    const recentMatches = matchDetails
        .sort((a, b) => new Date(b.date) - new Date(a.date))
        .slice(0, 3); // Get the first 3 elements

    return (
        <div>
            {/*<UnderProgress />*/}
            
            <h2 className={styles.absoluteHeading}>GameDay Analytics</h2>
            <div className={styles.spacingDiv}></div>
        <div className={styles.matchDetailsContainer}>
            <h1 className={styles.styleForh1}>Match Details</h1>
            <div className={styles.recentMatches}>
                <h2>Recent Matches</h2>
                <ul>
                    {recentMatches.slice(0, 3).map((match) => (
                        <li key={match.matchId}>
                            <p>
                                <span className={styles.recentMatchBolds}>{match.teamA} vs {match.teamB}</span> : <span className={styles.liScore}>{match.score}</span>
                            </p>
                            <p className={styles.recentMatchDate}>
                                <span className={styles.recentMatchBolds}>Played on:</span> {new Date(match?.date).toLocaleDateString()}
                            </p>
                        </li>
                    ))}
                </ul>
            </div>
            <label className={styles.matchDetailsHead} htmlFor="matchSelect">Match Details</label>
            <div className={styles.matchSelection}>
                <div>
                    <p>Search by teams</p>
                    <select id="matchSelect" onChange={handleMatchSelect} value={selectedMatch?.matchId || ''}>
                        <option value="">--Select Match--</option>
                        {matchDetails.map((match) => (
                            <option key={match.matchId} value={match.matchId}>
                                {match.teamA} vs {match.teamB}
                            </option>
                        ))}
                    </select>
                </div>
                <div>
                    <p>Search by teams</p>
                    <select id="matchSelect" onChange={handleMatchSelect} value={selectedMatch?.matchId || ''}>
                        <option value="">--Search by Date--</option>
                        {matchDetails.map((match) => (
                            <option key={match.matchId} value={match.matchId}>
                                {new Date(match.date).toLocaleDateString()}
                            </option>
                        ))}
                    </select>
                </div>
                <div>
                    <p>Search by Match ID</p>
                    <select id="matchSelect" onChange={handleMatchSelect} value={selectedMatch?.matchId || ''}>
                        <option value="">--Search by Match Id--</option>
                        {matchDetails.map((match) => (
                            <option key={match.matchId} value={match.matchId}>
                                {match.matchId}
                            </option>
                        ))}
                    </select>
                </div>


            </div>
            {selectedMatch && (
                <div className={styles.matchDetail}>
                    <h2>Match Details</h2>
                    <strong>Match Id: </strong><p className={styles.matchId}>{selectedMatch.matchId}</p>
                    <p><strong>Date:</strong> {new Date(selectedMatch.date).toLocaleDateString()}</p>
                    <p><strong>Teams:</strong> {selectedMatch.teamA} vs {selectedMatch.teamB}</p>
                    <p><strong>Score:</strong> {selectedMatch.score}</p>
                    <h3>Events:</h3>
                    <ul>
                        {selectedMatch.events.map((event, index) => (
                            <li key={index}>
                                <strong>Type:</strong> {event.eventType},
                                <strong> Time:</strong> {event.timestamp},
                                <strong> Players:</strong> {event.playersInvolved.join(', ')}
                            </li>
                        ))}
                    </ul>
                </div>
            )}
        </div>
        
        </div>

    );
};

export default MatchDetails;
